    url("./assets/fonts/Justus-Versalitas.woff") format("woff");
}

@font-face {
  font-family: "Justus Italic Oldstyle";
  font-style: normal;
  font-weight: normal;
  src: local("Justus Italic Oldstyle"),
    url("./assets/fonts/Justus-ItalicOldstyle.woff") format("woff");
}

@font-face {
  font-family: "Justus Italic";
  font-style: normal;
  font-weight: normal;
  src: local("Justus Italic"),
    url("./assets/fonts/Justus-Italic.woff") format("woff");
}

@font-face {
  font-family: "Justus Oldstyle";
  font-style: normal;
  font-weight: normal;
  src: local("Justus Oldstyle"),
    url("./assets/fonts/Justus-Oldstyle.woff") format("woff");
}

@font-face {
  font-family: "Justus Roman";
  font-style: normal;
  font-weight: normal;
  src: local("Justus Roman"),
    url("./assets/fonts/Justus-Roman.woff") format("woff");
}

@font-face {
  font-family: "Justus Bold";
  font-style: normal;
  font-weight: normal;
  src: local("Justus Bold"),
